body {
    margin: 0;
    overflow: hidden;
  }
  .App {
}
.canvas{
    position: fixed;
    top:0px;
    left:0px;
    z-index: 0;
}
.container{
    display:grid;
    grid-template-columns: minmax(100px, 200px), 1fr;
    grid-template-rows: 40px, 1fr
}
.header{
    display:grid;
    grid-template-columns: 400px, 1fr;
    grid-column-start: 1;
    grid-column-end: 1;
    grid-row-start: 1;
    grid-row-end: 1;
    position: fixed;
    top:0px;
    left: 0px;
    height: 40px;
    width: 100%;
    background-color: black;
    border-top: 0px;
    border-left: 0px;
    border-right: 0px;
    border-bottom: 1px;
    border-color: white;
    border-style: solid;
    z-index: 50000;
}
.subHead{
    font-size: 1em;
    color: white;
    margin: 10px;
    grid-column-start: 2;
    grid-column-end: 2;
    grid-row-start: 1;
    grid-row-end: 1;
}
.results{
    color: white;
    position: absolute;
    top: 40px;
    padding: 10px;
    grid-column-start: 1;
    grid-column-end: 1;
    grid-row-start: 2;
    grid-row-end: 2;
}
h1{
    font-size: 1em;
    color: white;
    margin: 10px;
}
h2{
    color: white;
    font-size: 1em;
}
.specimen{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
}
button {

    /* display: inline-block; */
    border: none;
      background: none;
      border-radius: 2px;
    /* padding: 1rem 2rem; */
    margin-left: 15px;
    padding-bottom: 10px;
    padding-top: 4px;
    text-decoration: none;
    height: 25px;
    overflow: hidden;
    width: 25px;
    color: white;
    font-family: inherit;
    font-size: 1.5em;
    font-weight: bold;
    line-height: 0;
    cursor: pointer;
    text-align: center;
    text-anchor: middle;
    /* transition: background 250ms ease-in-out, transform 150ms ease; */
    -webkit-appearance: none;
    -moz-appearance: none;
  }
  
  button:hover {
    color: white;
      background: none;
        -webkit-box-shadow: inset 0 0 3px 3px white;
    -moz-box-shadow: inset 0 0 3px 3px white;
    box-shadow: inset 0 0 3px 3px white;
    
  }
  /* button:focus {
      background: none;
      color: black;
  } */
  
  button:focus {
    outline: 0px solid white;
    outline-offset: -4px;
  
  }
  
  button:active {
    transform: scale(0.99);
    color:white;
       /* background: white; */
        -webkit-box-shadow: inset 0 0 3px 3px black;
    -moz-box-shadow: inset 0 0 3px 3px black;
    box-shadow: inset 0 0 3px 3px black;
    
  }
